import Vue from 'vue';
import Component from 'vue-class-component';
import { Action, State } from 'vuex-class';

@Component({})
export default class Comp extends Vue {

    @Action('showToast')
    showToast;

    @State(state => state.user.user.tel)
    phone: string;

    @State(state => state.user.aliPaymentId)
    alipay: string;
}



